.homepageImage{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}
.burgerImage{
    width: 47px;
    position: absolute;
    right: 0;
    top:15px;
    cursor: pointer;
    transition: .3s;
}
.burgerImage:hover{
    width: 48px;
}
.container{
    background-color: #222222;
    width:100vw;
    height: 100%;
    scroll-behavior: smooth;
}
